import React, { Component } from 'react';
import DatePicker from 'react-datepicker';
import "react-datepicker/dist/react-datepicker.css";
import { addDays } from 'date-fns';
import UserStore from '../stores/UserStore';

import '../styles/TimePicker.css';

class TimePicker extends Component {
    constructor(props) {
        super(props);
        this.state = {
            date: '',// new Date(),
            duration: '',
            purpose: '',
            appointments: [
                {
                    date: '2020. 9. 23.',
                    time: '15:30:00'
                },
            ],
            buttonDisabled: false,
        }
        //comment
        this.handleConfirm = this.handleConfirm.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.doAppointment = this.doAppointment.bind(this);
    };

    handleConfirm(date) {
        const newDate = ((this.state.date).toLocaleDateString());
        const newTime = ((this.state.date).toLocaleTimeString('en-GB'));
        alert((this.state.date).toLocaleDateString());
        alert((this.state.date).toLocaleTimeString('en-GB'));
        const newAppointment = {
            date: newDate,
            time: newTime
        }
        const appointments = [...this.state.appointments, newAppointment]
        this.setState({ appointments })
    }

    handleChange(date, name) {
        this.setState({[name]: date});
    }

    async doAppointment() {
        if (this.state.date == '' || this.state.duration == '' || this.state.purpose == '') {
            alert('Please fill out the required fields.') 
            return;
        }

        this.setState({
            buttonDisabled: true
        })
        
        try { 
            let res = await fetch('/appointment', {
                method: 'post',
                headers: {
                    'Accept': 'application/JSON',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    username: this.state.username,
                    password: this.state.password
                })
            });
            
            let result = await res.json();
            if (result && result.success) {
                UserStore.isLoggedIn = true;
                UserStore.username = result.username;
            }

            else if (result && result.success === false) {
                this.resetForm();
                alert(result.msg);
            }
        }
        catch(e) {
            console.log(e);
            this.resetForm();
        }
    }

    render() {
        const { date } = this.state;
        return (
            <div className="container">
                <div className="AppointmentPicker">
                    <DatePicker
                        name="date"
                        selected={date}
                        onChange={(date) => this.handleChange(date, "date")}
                        onSelect={this.handleClick}
                        dateFormat="MM/dd/yyyy HH:mm"
                        minDate={new Date()}
                        maxDate={addDays(new Date(), 7)}
                        timeintervals={30}
                        showTimeSelect
                    />
                    <input className="DurationPicker"
                        type="number"
                        name="duration"
                        onChange={(duration) => this.handleChange(duration, "duration")}
                    />
                    <input className="PurposePicker"
                        type="text"
                        name="purpose"
                        onChange={(purpose) => this.handleChange(purpose, "purpose")}
                    />
                    <button
                        className = "ConfirmButton"
                        onClick={this.doAppointment()}>
                        Confirm
                    </button>
                </div>
            </div>
        );
    }
}
export default TimePicker;